.auth{
    display: flex;
    align-items: center;
    gap: 7rem;
    margin: auto;
    padding-top: 13rem;
    justify-content: center;
}
.img{
    animation: slide-down 0.5s ease;
}
.form{
    width: 35rem;
    padding: 1.5rem;
    animation: slide-up 0.5s ease;
}
.form h2{
color: red;
text-align: center;
}
.form input{
  display: block;
  font-size: 1.2rem;
  padding: 0.7rem;
  margin: 1rem auto;
  width: 100%;
  border: 1px solid #777;
  border-radius: 3px;
  outline: none;

}
.form p {
    text-align: center;
    margin: 1rem;
}
.links{
  display: flex;
  justify-content: space-between;
    margin: 5px 0;
}
.register{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
@keyframes slide-up {
    0% {
      transform: translateY(-5rem);
    }
    100% {
      transform: translateY(0);
    }
  }
  @keyframes slide-down {
    0% {
      transform: translateY(5rem);
    }
    100% {
      transform: translateY(0);
    }
  }
  @media screen  and (max-width:800px){
    .img{
      display: none;
    }
  }
